:root {
  --sunrise-yellow: #ffde16;
  --sunrise-charcoal: #33342e;
  --sunrise-magenta: #8f0d56;
  --sunrise-orange: #fd9014;
  --sunrise-font: Source Sans Pro;
  --form-gutter-size: 0.4;
  --max-width: 1500px;
}

html {
  font-family: var(--sunrise-font);
  background-size: 200%;
  background-position: center;
  background-color: black;
}

body {
  width: 100%;
  height: 100%;
  margin: 0;
}

* {
  box-sizing: border-box;
}

main {
  position: relative;
  display: grid;
  grid-template-columns:
    auto
    minmax(0px, calc(var(--max-width) * calc(1 - var(--form-gutter-size))))
    minmax(300px, calc(var(--max-width) * var(--form-gutter-size)))
    auto;
  grid-template-rows: repeat(auto-fill, auto);
  place-items: stretch;
}
  main div.background {
    grid-column: 1 / span 4;
  }
  main section {
    grid-column: 2 / span 1;
    position: relative;
  }
  main div.background.video {
    grid-row: 1;
  }
  main section#video {
    grid-row: 1;
    grid-column: 1 / span 4;
  }
  main div.background.lead-in {
    grid-row: 2;
  }
  main section#lead-in {
    grid-row: 2;
  }
  main div.background.questions {
    grid-row: 3;
  }
  main section#questions {
    grid-row: 3;
  }
  main section#signup-form {
    grid-row: 2 / span 2;
    grid-column: 3 / span 1;
  }

div.background.video {
  background-image: url(/assets/images/header.jpg);
  position: relative;
  z-index: 1;
  background-position: center;
  background-size: cover;
  filter: blur(6px) grayscale(40%);
}
div.video-inner {
  opacity: 0.5;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(
    to right,
    var(--sunrise-magenta) 0%,
    var(--sunrise-magenta) 30%,
    var(--sunrise-orange) 50%,
    var(--sunrise-yellow) 100%
  );
}
section#video {
  height: 60vh;
  position: relative;
  z-index: 2;
  width: 100%;
}

section#video youtube-video {
  width: 100%;
  position: relative;
  height: 100%;
  opacity: 0;
  pointer-events: none;
  margin: 0 auto;
}

section#video youtube-video[player-state="playing"] {
  opacity: 1;
  transition: opacity 0.5s ease-in-out;
}

section#video youtube-video[player-state="playing"] + .loader,
section#video youtube-video[player-state="ended"] + .loader {
  transform: scale(0) !important;
}

section#video .loader {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: scale(1);
  transition: transform 0.5s ease-in-out;
}

section#video .loader .spinner {
  animation: rotator 1.4s linear infinite;
}

section#video .loader .path {
  stroke-dasharray: 187px;
  stroke-dashoffset: 0;
  transform-origin: center;
  animation:
    dash 1.4s ease-in-out infinite, 
    colors calc(1.4s * 4) ease-in-out infinite;
}

@keyframes rotator {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(270deg); }
}

@keyframes colors {
	0% { stroke: var(--sunrise-magenta); }
	25% { stroke: var(--sunrise-orange); }
	50% { stroke: var(--sunrise-yellow); }
	75% { stroke: var(--sunrise-orange); }
  100% { stroke: var(--sunrise-magenta); }
}

@keyframes dash {
 0% { stroke-dashoffset: 187px; }
 50% {
   stroke-dashoffset: calc(187px / 4);
   transform:rotate(135deg);
 }
 100% {
   stroke-dashoffset: 187;
   transform:rotate(450deg);
 }
}

div.background.lead-in {
  z-index: 2;
  position: relative;
  box-shadow: 0px 2px 12px 2px rgba(0,0,0,0.25), 0px -2px 12px 2px rgba(0,0,0,0.25);
}
div.lead-in-inner {
  background-color: var(--sunrise-charcoal);
  width: 100%;
  height: 100%;
  filter: brightness(130%);
}
section#lead-in {
  color: #fff;
  padding: 96px 64px;
  z-index: 2;
}
  section#lead-in h2 {
    margin: 0;
    font-size: 56px;
    line-height: 1;
    text-transform: uppercase;
    margin-bottom: 40px;
    color: var(--sunrise-yellow);
  }
  section#lead-in p {
    font-size: 24px;
  }

div.background.questions {
  z-index: 0;
  background-color: var(--sunrise-charcoal);
  position: relative;
  top: -40px;
  height: calc(100% + 40px);
}
section#questions {
  padding: 64px;
  color: #fff;
}
  section#questions h2 {
    font-size: 40px;
    line-height: 1;
    margin: 0;
    text-transform: uppercase;
  }
  section#questions dl {
    font-size: 20px;
  }
  section#questions dt {
    font-weight: 700;
    margin-bottom: 12px;
    font-size: 22px;
  }
  section#questions dd {
    margin: 0;
    margin-bottom: 24px;
  }

section#signup-form {
  position: relative;
  padding: 0 16px;
  z-index: 3;
}
  section#signup-form action-network-form {
    position: sticky;
    top: 32px;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0px 2px 12px 2px rgba(0,0,0,0.25);
    max-height: calc(100vh - 64px);
    margin-top: -32px;
    background-image: linear-gradient(
      135deg,
      var(--sunrise-magenta) 0%,
      var(--sunrise-magenta) 30%,
      var(--sunrise-orange) 100%
    );
  }